import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'

Vue.use(Vuex)

const API_URL = 'http://127.0.0.1:8000/api'

export default new Vuex.Store({
  state: {
    token: '',
    username: '',
    ticket: {},
    tickets: []
  },
  mutations: {
    login (state, { username, token }) {
      state.username = username
      state.token = token
    },
    logout (state) {
      state.token = ''
      state.username = ''
    },
    ticketSet (state, ticket) {
      let ticketIndex = 0
      for (let i = 0; i < state.tickets.length; i++) {
        if (state.tickets[i].id === ticket.id) {
          ticketIndex = i
          break
        }
      }
      state.tickets[ticketIndex].status = ticket.status
      state.tickets[ticketIndex].completed = ticket.completed
    },
    ticketsSet (state, tickets) {
      state.tickets = tickets
    }
  },
  actions: {
    async login (context, credentials) {
      try {
        const response = await axios.post(`${API_URL}/auth/`, credentials)
        const token = response.data.token
        localStorage.setItem('token', token)
        axios.defaults.headers.common.Authorization = 'Token ' + token
        const username = credentials.username
        context.commit('login', { username, token })
      } catch (error) {
        if (error.response.status === 400) {
          context.commit('error', 'bad credentials')
        }
      }
    },
    logout (context) {
      context.commit('logout')
      localStorage.removeItem('token')
      delete axios.defaults.headers.common.Authorization
    },
    async getTicket (context, id) {
      const response = await axios.get(`${API_URL}/tickets/${id}/`)
      context.commit('ticketSet', response.data)
    },
    async approveTicket (context, id) {
      try {
        await axios.post(`${API_URL}/tickets/${id}/approve/`)
      } catch (error) {
        if (error.response.status === 403) {
          alert('You have to be the approver to approve this ticket')
        }
      }
    },
    async changeTicket (context, id) {
      try {
        const response = await axios.post(`${API_URL}/tickets/${id}/changes/`)
        context.commit('ticketSet', response.data)
      } catch (error) {
        if (error.response.status === 403) {
          alert('You have to be the author to request changes to this ticket')
        }
      }
    },
    async completeTicket (context, id) {
      try {
        const response = await axios.post(`${API_URL}/tickets/${id}/complete/`)
        context.commit('ticketSet', response.data)
      } catch (error) {
        if (error.response.status === 403) {
          alert('You have to be author or assignee to complete this ticket')
        }
      }
    },
    async closeTicket (context, id) {
      try {
        const response = await axios.post(`${API_URL}/tickets/${id}/close/`)
        context.commit('ticketSet', response.data)
      } catch (error) {
        if (error.response.status === 403) {
          alert('You have to be superuser to close this ticket')
        }
      }
    },
    async getTickets (context) {
      const response = await axios.get(`${API_URL}/tickets/`)
      context.commit('ticketsSet', response.data)
    }
  },
  modules: {
  }
})
