<template>
    <ul>
        <li v-for="ticket in tickets" :key="ticket.id">
            <Ticket :ticket="ticket"/>
        </li>
    </ul>

</template>

<script>
import Ticket from '@/components/Ticket.vue'
import { mapActions, mapState } from 'vuex'

export default {
  name: 'TicketList',
  computed: {
    ...mapState(['tickets'])
  },
  methods: {
    ...mapActions(['getTickets'])
  },
  mounted () {
    this.getTickets()
  },
  components: {
    Ticket
  }

}
</script>
