<template>
  <div class="hello">
    <h1>{{ ticket.title }}</h1>
    <p>Author: {{ ticket.author }}</p>
    <p>Description: {{ ticket.description }}</p>
    <p>Status: {{ ticket.status }}</p>
    <p>Completed: {{ ticket.completed }}</p>
    <button @click="approve(ticket.id)">Approve</button>
    <button @click="completeTicket(ticket.id)">Complete</button>
    <button @click="changeTicket(ticket.id)">Changes</button>
    <button @click="closeTicket(ticket.id)">Close</button>
  </div>
</template>

<script>
import { mapActions } from 'vuex'

export default {
  name: 'Ticket',
  props: [
    'ticket'
  ],
  methods: {
    ...mapActions(['getTicket', 'approveTicket', 'completeTicket', 'changeTicket', 'closeTicket']),
    async approve (id) {
      await this.approveTicket(id)
      await this.getTicket(id)
    }
  }
}
</script>
